</template>
  
<script>
import { mapActions } from 'vuex';
import feather from 'feather-icons';

export default {
  name: 'LayoutNavbar',
  data() {
    return {
      user_rol: null
    }
  },
  computed: {
    userName() {
      return this.$store.getters.get__user.datos.nombres;
    },
    userRole() {
      return this.$store.getters.get__user.usu_rol;
    },
  },
  methods: {
    ...mapActions(['get', 'post']),

    getRole(){
      if(this.userRole === 'ADMIN'){
        this.user_rol = 'Administrador';
      }else if(this.userRole === 'ABOGADO'){
        this.user_rol = 'Abogado';
      }else if(this.userRole === 'SECRETARIA'){
        this.user_rol = 'secretaria';
      }
    },

    async mtdLogout (){
   
   try {
             this.$showLoadingAlert('', 'Cerrando Sesin...');
             const formData = {
                 id:null
             }
             const response = await this.$postData('demandante/logout', this, formData);
             this.$closeLoadingAlert();
             if (response.state === 0) {
               this.$store.commit('mt_logout_auth');
               this.$router.replace({ path: '/' });
               this.$successAlert('',('Sesin Finalizada'))
             }
         } catch (error) {
           this.$closeLoadingAlert();
             this.$handleApiError(error, error.message);
             this.$errorAlert('', error); 
         }
   this.clearLocalStorage();
 },
  },
  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
  },

  created() {
    this.getRole();
  }
};
</script>
